.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.modegroup {
  display: flex;
  align-items: center;
  grid-gap: 10px;
}
.modegroup .modeInput {
  position: relative;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  border: 2px solid;
}
.modegroup .modeInput input {
  width: 25px;
  height: 25px;
  position: absolute;
  left: -2px;
  top: -2px;
  opacity: 0;
  cursor: pointer;
}
.modegroup .modeInput.MI1 {
  background: white;
  border-color: white;
}
.modegroup .modeInput.MI2 {
  background: green;
  border-color: green;
}
.modegroup .modeInput.MI3 {
  background: blue;
  border-color: blue;
}
.modegroup .modeInput:has(input:checked) {
  border-color: #000;
}